<template>
  <v-app dark>
    <v-content>
    <v-container class="text-xs-center" fluid>
    <!-- Title of page -->
    <v-flex mb-4>
    <div class="text-xs-center">
    <h1 class="display-3">{{title}}</h1>
    <h3 class="headline">{{subHeading}}</h3>
    </div>
    </v-flex>
    <!-- INVOICE INPUT CONTAINER -->
    <!-- BUSINESS INFO -->
    <v-layout row wrap justify-space-around px-2 pt-4>
    <v-flex px-2>
      <v-card height="100%" class="invoiceHeading" style="background: rgba(108,212,255, 0.2)">
        <v-flex px-4 py-2>
      <v-text-field placeholder="Your Business Name" v-model="invoice.businessName"></v-text-field>
      <!-- <p>{{invoice.businessName}}</p> -->
      <v-text-field placeholder="Address Line 1" v-model="invoice.addressLine1"></v-text-field>
      <!-- <p>{{invoice.addressLine1}}</p> -->
      <v-text-field placeholder="Address Line 2" v-model="invoice.addressLine2"></v-text-field>
      <!-- <p>{{invoice.addressLine2}}</p> -->
      <v-text-field placeholder="Address Line 3" v-model="invoice.addressLine3"></v-text-field>
      <!-- <p>{{invoice.addressLine3}}</p> -->
      <v-text-field placeholder="Bill to" v-model="invoice.billTo"></v-text-field>
      <!-- <p>{{invoice.billTo}}</p> -->
      </v-flex>
      </v-card>
    </v-flex>
    <!-- INVOICE INFO -->
    <v-flex sm-12 px-2>
      <v-card height="100%" class="invoiceNumber" style="background: rgba(255,131,96, 0.2)">
        <v-flex px-4 py-2>
    <div>
      <v-text-field placeholder="Invoice Number" v-model="invoice.invoiceNumber"></v-text-field>
      <!-- <p>{{invoice.invoiceNumber}}</p> -->

      <v-text-field placeholder="Date" v-model="invoice.date"></v-text-field>
      <!-- <p>{{invoice.date}}</p> -->
      <v-text-field placeholder="Balance Due" v-model="invoice.balance"></v-text-field>
      <!-- <p>{{invoice.balance}}</p> -->
    </div>
    </v-flex>
    </v-card>
    </v-flex>
    </v-layout>

    <!-- INVOICE ITEMS AND QUANTITIES -->
    <v-layout row wrap>
    <v-flex xs-12 px-3 pt-2>
      <v-card  class="items" style="background: rgba(0,255,245, 0.2)">
        <v-flex px-4 py-2>
    <div class="invoiceQuantities">
      <v-text-field placeholder="Item/Description" v-model="invoice.item"></v-text-field>
      <!-- <p>{{invoice.item}}</p> -->

      <v-text-field placeholder="Quantity" v-model="invoice.quantity"></v-text-field>
      <!-- <p>{{invoice.quantity}}</p> -->
      <v-text-field placeholder="Rate" v-model="invoice.rate"></v-text-field>
      <!-- <p>{{invoice.rate}}</p> -->
    </div>
    </v-flex>
    </v-card>
    </v-flex>
    </v-layout>

        <!-- NOTES -->
    <v-layout row wrap>
    <v-flex xs-6 px-3 pt-2>
      <v-card  class="notes" style="background: rgba(5, 255, 176, 0.2)">
        <v-flex px-4 py-2>
    <div>
      <v-textarea placeholder="notes" v-model="invoice.notes"></v-textarea>
      <!-- <p>{{invoice.notes}}</p> -->

    </div>
    </v-flex>
    </v-card>
    </v-flex>
    </v-layout>
    <v-btn light class="primary space" v-on:click="createPdf">Create Invoice</v-btn>
    <v-btn light class="success space" v-on:click="preview = !preview">Preview Invoice</v-btn>

  </v-container>
  </v-content>

  <!--  Invoice Preview -->
  <v-card v-if="preview" light class="invoice-demo" pa-4 ma-4>
    <h1 class="text-xs-center invoice-heading">Invoice</h1>
    <hr>
<v-container fluid pa-4>
    <v-layout row wrap justify-space-around>
      <v-flex xs-6 offset-xs1>
  <div class="top-left">
    <h2>{{invoice.businessName}}</h2>
    <p>{{invoice.addressLine1}}</p>
    <p>{{invoice.addressLine2}}</p>
    <p>{{invoice.addressLine3}}</p>
    <h3>{{invoice.billTo}}</h3>
  </div>
  </v-flex>

<v-flex xs-3></v-flex>
<v-flex xs-3>
  <div class="top-right">
    <h3>Invoice # {{invoice.invoiceNumber}}</h3>
    <p>Date: {{invoice.date}}</p>
    <p>Amount Owed: {{invoice.balance}}</p>
  </div>
  </v-flex>
  </v-layout>
  </v-container>

  <hr>
  <div class="item-heading">
  <h3 class="text-xs-center">Item(s)</h3>
  </div>

<v-container fluid pa-4>
  <v-layout row wrap>
    <v-flex xs-12 pl-1 offset-xs1>
  <div class="middle">
    <p>Item/Description: {{invoice.item}}</p>
    <p>Quantity: {{invoice.quantity}}</p>
    <p>Rate: £{{invoice.rate}}</p>
  </div>
      </v-flex>
  </v-layout>
</v-container>

  <hr>
  <div class="note-heading">
  <h3 class="text-xs-center">Notes</h3>
  </div>
  <v-container fluid pa-4>
  <v-layout row wrap>
    <v-flex xs-12 pl-1 offset-xs1>
  <div class="bottom">
    <p>{{invoice.notes}}</p>
  </div>
        </v-flex>
  </v-layout>
</v-container>

  </v-card>

  </v-app>
</template>

<script>
import Pdf from 'jspdf'
import JQuery from 'jquery'
import html2canvas from 'html2canvas'

export default {
  name: 'HelloWorld',
  components: {
    Pdf,
    JQuery,
    html2canvas
  },
  data () {
    return {
      title: 'Welcome to Invoicer',
      subHeading: 'Simple way to generate Invoices',
      preview: false,
      invoice: {
        businessName: '',
        addressLine1: '',
        addressLine2: '',
        addressLine3: '',
        billTo: '',
        invoiceNumber: '',
        date: '',
        balance: '',
        item: '',
        quantity: '',
        rate: '',
        notes: ''
      }
    }
  },
  methods: {
    createPdf () {
      var doc = new Pdf()
      const J = JQuery
      var specialElementHandlers = {
        '#editor': function (element, renderer) {
          return true
        }
      }

      doc.setFont('helvetica')
      doc.setFontSize(22)
      doc.setFontType('bold')

      doc.text(90, 15, 'Invoice')
      doc.setLineWidth(0.5)

      doc.line(20, 30, 190, 30)
      doc.fromHTML(J('.top-left').html(), 30, 35, {
        'width': 170,
        'elementHandlers': specialElementHandlers
      })
      doc.fromHTML(J('.top-right').html(), 150, 35, {
        'width': 170,
        'elementHandlers': specialElementHandlers
      })

      doc.line(20, 100, 190, 100)
      doc.fromHTML(J('.item-heading').html(), 90, 100, {
        'width': 170,
        'elementHandlers': specialElementHandlers
      })
      doc.fromHTML(J('.middle').html(), 30, 120, {
        'width': 170,
        'elementHandlers': specialElementHandlers
      })

      doc.line(20, 200, 190, 200)
      doc.fromHTML(J('.note-heading').html(), 90, 200, {
        'width': 170,
        'elementHandlers': specialElementHandlers
      })
      doc.fromHTML(J('.bottom').html(), 30, 220, {
        'width': 170,
        'elementHandlers': specialElementHandlers
      })

      // doc.rect(100, 100, 20, 20)
      doc.setFont('helvetica')
      doc.setFontSize(9)
      doc.setFontType('bold')
      doc.setLineWidth(0.5)
      doc.save('a4test.pdf')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.invoiceHeading{
  /* background: rgba(108,212,255, 0.2); */
}
.invoiceNumber{
  /* background: rgba(255,131,96, 0.2); */
}
.items{
  /* background: rgba(0,255,245, 0.2) */
}
.notes{
  /* background: rgba(5, 255, 176, 0.2); */
}
.space{
  margin:2em;
}
hr{
  width:80%;
  margin: 0 auto;
  border:none;
  background-color: black;
  height:1.5px;
}
.invoice-demo{
  width:90%;
  margin:0 auto;
  height:597px;
}
</style>



