import Vue from 'vue'
import Router from 'vue-router'
import Invoicer from '@/components/Invoicer'
import 'vuetify/dist/vuetify.min.css'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Invoicer',
      component: Invoicer
    }
  ]
})



